function forms() {
  //   
  let phone = document.querySelectorAll('input[name="phone"]'); //    

  phone.forEach(function (item) {
    item.addEventListener('input', function () { //      +
      this.value = this.value.replace(/[^\+\d]/g, '');
    });
  });

  // 
  let message = { //    
    loading: '...',
    success: '!     ',
    failure: '-   ...',
  };

  //   
  let form = document.querySelector('.main-form'), //     
    secondForm = document.getElementById('form'),
    input = form.getElementsByTagName('input'), // 
    secondInput = secondForm.getElementsByTagName('input'),
    statusMassege = document.createElement('div'); //    

  statusMassege.classList.add('status'); //  

  function postData(data) { //    
    let formData = new FormData(data);
    let obj = {};
    formData.forEach(function (value, key) {
      obj[key] = value;
    });
    let json = JSON.stringify(obj);

    return new Promise(function (resolve, reject) { //  
      let request = new XMLHttpRequest();
      request.open('POST', 'server.php');
      request.setRequestHeader('Content-Type', 'application/json; charset = utf-8');
      request.onreadystatechange = function () {
        if (request.readyState < 4) { //   
          statusMassege.innerHTML = message.loading;
        } else if (request.readyState === 4 && request.status == 200) { //   
          resolve();
        } else { //  
          reject();
        }
      };
      request.send(json);
    });
  }

  form.addEventListener('submit', function (event) { //    
    event.preventDefault(); //   
    form.appendChild(statusMassege);

    postData(form) //       
      .then(() => statusMassege.innerHTML = message.success) //   
      .catch(() => statusMassege.innerHTML = message.failure); //  

    for (let i = 0; i < input.length; i++) { //   
      input[i].value = '';
    }
  });

  //  
  secondForm.addEventListener('submit', function (event) { //    
    event.preventDefault(); //   
    secondForm.appendChild(statusMassege); //    
    statusMassege.style.color = '#ffffff';

    postData(secondForm)
      .then(() => statusMassege.innerHTML = message.success)
      .catch(() => statusMassege.innerHTML = message.failure);

    for (let i = 0; i < secondInput.length; i++) { //   
      secondInput[i].value = '';
    }
  });
}

module.exports = forms;